<template>
  <div class="table">
    <el-table :data="dataList" border style="width: 100%" @sort-change="handleSortChange" height="600">
      <el-table-column fixed prop="id" label="ID" min-width="80" sortable="custom">
      </el-table-column>
      <el-table-column label="活动名称" min-width="180">
        <template slot-scope="scope">
          <el-button type="text" @click="goDetailPage(scope.row)" v-if="!isCheckpage">{{scope.row.name}}</el-button>
          <span v-if="isCheckpage">{{scope.row.name}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="type" label="页面类型" min-width="120" sortable="custom">
        <template slot-scope="scope">
          <span v-if="scope.row.type===1">卡片</span>
          <span v-if="scope.row.type===2">开屏页面</span>
        </template>
      </el-table-column>
      <el-table-column prop="state" label="状态" min-width="120" sortable="custom">
        <template slot-scope="scope">
          <span v-if="scope.row.state===-1" style="color:red">禁用</span>
          <span v-if="scope.row.state===0" style="color:gray">草稿</span>
          <span v-if="scope.row.state===3" style="color:blue">待审核</span>
          <span v-if="scope.row.state===6" style="color:Orange">待上架</span>
          <span v-if="scope.row.state===9" style="color:green">已上架</span>
          <span v-if="scope.row.state===12" style="color:black">已下架</span>
        </template>
      </el-table-column>
      <el-table-column prop="startTime" label="开始时间" min-width="170" sortable="custom">
      </el-table-column>
      <el-table-column prop="endTime" label="结束时间" min-width="170" sortable="custom">
      </el-table-column>
      <el-table-column prop="stateModifier" label="上次修改人" min-width="210" sortable="custom">
      </el-table-column>
      <el-table-column label="操作" min-width="270">
        <template slot-scope="scope">
          <el-button v-if="scope.row.state===6" @click="shangjia(scope.row)" size="mini" type="success">上架</el-button>
          <el-button v-if="scope.row.state===9" @click="xiajia(scope.row)" size="mini" type="warning">下架</el-button>
          <el-button v-if="scope.row.state===9||scope.row.state===12" @click="jinyong(scope.row)" size="mini" type="warning">禁用</el-button>
          <el-button v-if="scope.row.upperOperateState!==-1&&scope.row.upperOperateState!==2&&(scope.row.state===0||scope.row.state===12||scope.row.state===-1)" @click="goEditPage(scope.row)" size="mini" type="primary">编辑</el-button>
          <el-button v-if="scope.row.state===0" @click="handleDelete(scope.row)" size="mini" type="danger">删除</el-button>
          <el-button v-if="scope.row.state===9||scope.row.state===12||scope.row.state===-1" @click="goDataPage(scope.row)" size="mini" type="primary" disabled>查看数据</el-button>
          <el-button v-if="isCheckpage&&scope.row.state===3" @click="goCheckPage(scope.row)" size="mini" type="primary">审核</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- <el-dialog title="提示" :visible.sync="shangjiaDialogVisible" width="600px" :before-close="shangjiaDialogClose" center>
      <div class="shangjia-div">
        <span class="shangjia-text1">上架后，客户端即可以拉取到活动数据（只在指定时间内显示），请确认是否涉及到保密原则！</span>
        <div class="text-line">
          <el-radio v-model="shangjiaData.shangjiaType" :label="1" :disabled="shangjiaData.tiqianDisable">提前上架</el-radio>
          <el-date-picker v-model="shangjiaData.time" type="datetime" placeholder="请选择上架时间" size='mini' class="shangjia-date" :disabled="shangjiaData.tiqianDisable">
          </el-date-picker>
        </div>
        <span class="shangjia-text2">上架后客户端拉取卡片数据并做缓存，提高触达，请确认是否涉及保密。</span>
        <el-radio v-model="shangjiaData.shangjiaType" :label="2">活动出现</el-radio>
        <span class="shangjia-text2">在卡片指定时间区间内从服务器拉取数据，直接生效。（若未设置触发时间，则从当前时间计算）</span>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="shangjiaDialogClose(true)">取 消</el-button>
        <el-button type="primary" @click="shangjiaDialogClose()">确 定</el-button>
      </span>
    </el-dialog> -->
    <el-dialog title="下架申请" :visible.sync="xiajiaDialogVisible" width="600px" :before-close="xiajiaDialogClose" center>
      <el-form :model="xiajiaData" ref="xiajiaData" label-width="100px">
        <el-form-item label="下架原因" prop="reason" :rules="applyRules">
          <el-input v-model="xiajiaData.reason" :autosize="{ minRows: 2, maxRows: 3}" type="textarea" placeholder="请填写下架原因" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{xiajiaData.reason?xiajiaData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="xiajiaDialogClose(true)">取 消</el-button>
        <el-button type="primary" @click="xiajiaDialogClose()">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="禁用申请" :visible.sync="jinyongDialogVisible" width="600px" :before-close="jinyongDialogClose" center>
      <el-form :model="jinyongData" ref="jinyongData" label-width="100px">
        <el-form-item label=" 禁用原因" prop="reason" :rules="applyRules">
          <el-input v-model="jinyongData.reason" :autosize="{ minRows: 2, maxRows: 3}" type="textarea" placeholder="请填写禁用原因" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{jinyongData.reason?jinyongData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="jinyongDialogClose(true)">取 消</el-button>
        <el-button type="primary" @click="jinyongDialogClose()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "table-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    },
    dataList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    var specialValidate = (rule, value, callback) => {
      if (/^\s+$/.test(value)) {
        callback(new Error("不能输入全空格"));
      } else {
        callback();
      }
    };
    return {
      jinyongDialogVisible: false,
      jinyongData: {},
      xiajiaDialogVisible: false,
      xiajiaData: {},
      applyRules: [
        { required: true, message: "内容不能为空", trigger: "blur" },
        { required: true, validator: specialValidate, trigger: "blur" }
      ]
      //shangjiaDialogVisible: false,
      // shangjiaData: {
      //   tiqianDisable: false,
      //   shangjiaType: 1
      // },
      // dataList: [
      //   {
      //     id: 2,
      //     name: "双十二活动卡片",
      //     type: 1,
      //     state: 9,
      //     startTime: "1971-01-01 00:00:00",
      //     endTime: "1971-01-01 00:00:00",
      //     stateModifier: "小王"
      //   },
      //   {
      //     id: 4,
      //     name: "双十二开屏卡片",
      //     type: 2,
      //     state: 12,
      //     startTime: "1971-01-01 22:22:00",
      //     endTime: "2019-01-01 00:00:00",
      //     stateModifier: "小王"
      //   }
      // ]
    };
  },
  methods: {
    //删除操作
    handleDelete(data) {
      let message = "是否确定删除“" + data.name + "”的草稿？";
      this.$confirm(message, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          let param = {
            id: data.id,
            versionCode: data.versionCode
          };
          this.$http
            .post("/scene/del", param)
            .then(response => {
              if (response.data && response.data.code == "0") {
                this.refreshData();
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
              } else {
                if (response.data.msg) {
                  this.$message.error(response.data.msg);
                } else {
                  this.$message.error("删除失败！");
                }
              }
            })
            .catch(() => {
              this.$message.error("删除失败！");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    //跳转数据浏览页面
    goDataPage(data) {
      this.$emit("table-event", {
        type: "data-page",
        data: data
      });
    },
    //跳转编辑页面
    goEditPage(data) {
      let endTime = Date.parse(data.endTime);
      if (data.state !== 0 && Date.now() > endTime) {
        this.$confirm("活动已结束, 无法编辑！", "提示", {
          confirmButtonText: "确定",
          showCancelButton: false,
          type: "warning",
          center: true
        });
      } else {
        this.$emit("table-event", {
          type: "edit-page",
          data: data
        });
      }
    },
    //禁用弹窗
    jinyongDialogClose(done) {
      if (typeof done === "function") {
        this.jinyongData = {};
        done();
      } else if (done) {
        this.jinyongData = {};
        this.jinyongDialogVisible = false;
      } else {
        this.$refs["jinyongData"].validate(valid => {
          if (valid) {
            let param = {
              id: this.jinyongData.id,
              versionCode: this.jinyongData.versionCode,
              lastState: this.jinyongData.state,
              state: -1,
              explain: this.jinyongData.reason
            };
            this.$http
              .post("/scene/modifyState", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.refreshData();
                  this.$message({
                    type: "success",
                    message: "禁用成功"
                  });
                  this.xiajiaData = {};
                  this.jinyongDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("禁用失败！");
                  }
                }
              })
              .catch(() => {
                this.$message.error("禁用失败！");
              });
          } else {
            this.$message.warning("请按要求填写");
            return false;
          }
        });
      }
    },
    //禁用操作
    jinyong(data) {
      this.jinyongData = data;
      this.jinyongDialogVisible = true;
    },
    //下架弹窗
    xiajiaDialogClose(done) {
      if (typeof done === "function") {
        this.xiajiaData = {};
        done();
      } else if (done) {
        this.xiajiaData = {};
        this.xiajiaDialogVisible = false;
      } else {
        this.$refs["xiajiaData"].validate(valid => {
          if (valid) {
            let param = {
              id: this.xiajiaData.id,
              versionCode: this.xiajiaData.versionCode,
              lastState: this.xiajiaData.state,
              state: 12,
              explain: this.xiajiaData.reason
            };
            this.$http
              .post("/scene/modifyState", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.refreshData();
                  this.$message({
                    type: "success",
                    message: "下架成功"
                  });
                  this.xiajiaData = {};
                  this.xiajiaDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("下架失败！");
                  }
                }
              })
              .catch(() => {
                this.$message.error("下架失败！");
              });
          } else {
            this.$message.warning("请按要求填写");
            return false;
          }
        });
      }
    },
    //下架操作
    xiajia(data) {
      this.xiajiaData = data;
      this.xiajiaDialogVisible = true;
    },
    // shangjiaDialogClose(done) {
    //   if (typeof done === "function") {
    //     this.shangjiaData = {};
    //     done();
    //   } else if (done) {
    //     this.shangjiaData = {};
    //     this.shangjiaDialogVisible = false;
    //   } else {
    //   }
    // },
    //上架操纵
    shangjia(data) {
      let param = {
        id: data.id,
        versionCode: data.versionCode,
        lastState: data.state,
        state: 9
      };
      this.$http
        .post("/scene/modifyState", param)
        .then(response => {
          if (response.data && response.data.code == "0") {
            this.refreshData();
            this.$message({
              type: "success",
              message: "上架成功"
            });
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("上架失败！");
            }
          }
        })
        .catch(() => {
          this.$message.error("上架失败！");
        });
    },
    //跳转详情页面
    goDetailPage(data) {
      this.$emit("table-event", {
        type: "detail-page",
        data: data
      });
    },
    //跳转审核页面
    goCheckPage(data) {
      this.$emit("table-event", {
        type: "check-page",
        data: data
      });
    },
    //刷新数据
    refreshData() {
      this.$emit("table-event", {
        type: "refresh-data"
      });
    },
    //排序事件
    handleSortChange(data) {
      let field = data.prop;
      let order = "";
      if (data.order === "ascending") {
        order = "asc";
      } else if (data.order === "descending") {
        order = "desc";
      }
      this.$emit("table-event", {
        type: "sortChange",
        data: {
          field: field,
          order: order
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.table {
  width: 100%;
  margin-top: 30px;
}
.shangjia-div {
  display: flex;
  flex-direction: column;
}
.text-line {
  display: flex;
  align-items: center;
}
.shangjia-date {
  margin-left: 7px;
}
.shangjia-text1 {
  color: rgb(192, 0, 0);
  margin-bottom: 20px;
}
.shangjia-text2 {
  font-size: 10px;
  color: rgb(196, 196, 196);
  margin-bottom: 20px;
}
</style>