<template>
  <div class="activity-container" v-loading="uploadLoading">
    <div class="maintitle-class box-class" v-if="isEditPage">
      <span class="main-title">创建新卡片</span>
      <div class="steps-class">
        <el-steps :active="stepNumber" :align-center="true">
          <el-step title="运营规则设置" icon="el-icon-edit"></el-step>
          <el-step title="信息确认" icon="el-icon-upload"></el-step>
          <el-step title="提交审核" icon="el-icon-success"></el-step>
        </el-steps>
      </div>
    </div>
    <div class="activity-preview box-class" v-if="stepNumber!==3">
      <span class="title-class">卡片预览</span>
      <div class="card-box">
        <img v-if="cardPreview" :src="cardPreview" class="card-class">
        <div v-if="!cardPreview" class="card-class" style="border: 1px solid #000"></div>
        <img v-if="expandPreview" :src="expandPreview" class="card2-class">
        <div v-if="!expandPreview" class="card2-class" style="border: 1px solid #000"></div>
      </div>
    </div>
    <div class="rules-area box-class" v-if="isEditPage&&stepNumber===1">
      <span class="title-class">发布策略</span>
      <el-form label-position="left" ref="imeiData" :model="imeiData" label-width="120px" size="small">
        <el-form-item label="发布方式：" prop="releaseType" required>
          <el-radio-group v-model="imeiData.releaseType">
            <el-radio :label="1">全量发布</el-radio>
            <el-radio :label="2">灰度发布</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="控制方式：" prop="controlType" v-if="imeiData.releaseType===2" required>
          <el-radio-group v-model="controlType">
            <el-radio :label="1">IMEI控制</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="定向灰度：" prop="grayscaleType" v-if="imeiData.releaseType===2" required>
          <el-radio-group v-model="grayscaleType">
            <el-radio :label="1">指定IMEI</el-radio>
            <!-- <el-radio :label="2">屏蔽IMEI</el-radio> -->
          </el-radio-group>
        </el-form-item>
        <el-form-item label="上传IMEI方式：" prop="grayscaleDataType" v-if="imeiData.releaseType===2">
          <el-checkbox-group v-model="grayscaleDataTypes" size="small" @change="grayscaleDataTypeChange">
            <el-checkbox :label="1">手动填写(≤10个)</el-checkbox>
            <el-checkbox :label="2">上传文件(txt格式，每行一个IMEI码，请不要添加其他字符)</el-checkbox>
          </el-checkbox-group>
          <div class="time-box" v-if="imeiData.grayscaleDataType===1">
            <el-input v-model="imeiValue" placeholder="请输入IMEI" style="width:200px;margin-right: 20px;"></el-input>
            <el-button type="primary" round size="mini" @click="saveIMEI()">添加</el-button>
          </div>
          <template v-if="imeiData.grayscaleDataType===1">
            <el-tag v-for="(imei, index) in imeiList" :key="index" closable type="info" @close="deleteIMEI(index)" style="margin-right: 5px;">
              {{imei}}
            </el-tag>
          </template>
          <div v-if="imeiData.grayscaleDataType===2&&!this.excelFile">
            <a class='download' :href='downloadFileUrl' download="下载" title="下载" v-if="downloadFileName" target="_blank">{{downloadFileName}}</a>
          </div>
          <el-upload v-if="imeiData.grayscaleDataType===2" ref="upload" action="ceshi" style="width:400px" :http-request="getExcelFile" :before-remove="removeFile" :limit="1" :on-exceed="handleExceed">
            <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="尾数灰度：" v-if="imeiData.releaseType===2">
          <el-checkbox v-model="isMantissaList1">使用1个尾数</el-checkbox>
          <el-checkbox-group v-model="mantissaList1" v-if="isMantissaList1">
            <el-checkbox v-for="n in 10" :label="getForNumber(n,1)" :key="getForNumber(n,1)" style="margin-left:0px !important;margin-right:20px !important">{{getForNumber(n,1)}}</el-checkbox>
          </el-checkbox-group>
          <el-checkbox v-model="isMantissaList2">使用2个尾数</el-checkbox>
          <el-checkbox-group v-model="mantissaList2" v-if="isMantissaList2">
            <el-checkbox v-for="n in 100" :label="getForNumber(n,2)" :key="getForNumber(n,2)" style="margin-left:0px !important;margin-right:20px !important">{{getForNumber(n,2)}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <span class="title-class">触发与消失规则</span>
      <el-form label-position="left" ref="basicData" :model="basicData" label-width="120px" size="small" :rules="peopleRules">
        <!-- <el-form-item label="人群标签：" prop="peopleLabels">
                    <el-select v-model="basicData.peopleLabels" placeholder="请选择人群标签（可多选）" multiple>
                        <el-option v-for="item in peopleLabels" :key="item.key" :label="item.value" :value="item.key">
                        </el-option>
                    </el-select>
                </el-form-item> -->
        <el-form-item label="绑定维度：" prop="bindType">
          <el-radio-group v-model="basicData.bindType">
            <el-radio disabled :label="1">按空间</el-radio>
            <el-radio :label="2">按时间</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <template v-if="basicData.bindType===2">
        <span class="title-class">时间选择</span>
        <el-form label-position="left" ref="timeData" :model="timeData" label-width="120px" size="small" :rules="timeRules">
          <el-form-item label="时间类型：" required>
            <el-radio-group v-model="timeData.type" :disabled="startTimeDisabled" @change="timeRadioChoose">
              <el-radio :label="0">手动填写</el-radio>
              <el-radio :label="1">节假日</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="节假日选择：" v-if="timeData.type===1" required>
            <div class="time-box">
              <el-select v-model="timeData.holiday" placeholder="请选择一个节日" clearable @change="holidayChange" :disabled="startTimeDisabled" style="margin-right: 20px;">
                <el-option v-for="item in holidayList" :key="item.value" :label="item.value" :value="item.value">
                </el-option>
              </el-select>
              <el-date-picker @change="dateChange(true)" v-model="timeData.startTime" type="datetime" placeholder="选择生效时间" v-if="timeData.holiday" :disabled="startTimeDisabled">
              </el-date-picker>
              <div style="margin-left:10px;margin-right:10px" v-if="timeData.holiday">至</div>
              <el-date-picker @change="dateChange(false)" v-model="timeData.endTime" type="datetime" placeholder="选择失效时间" v-if="timeData.holiday">
              </el-date-picker>
            </div>
            <el-alert title="失效时间需要在生效时间之后" type="error" v-if="isTimeError1" :closable="false"></el-alert>
            <el-alert title="不可设置为过去时间" type="error" v-if="isTimeError2" :closable="false"></el-alert>
          </el-form-item>
          <el-form-item label="手动填写：" v-if="timeData.type===0" required>
            <div class="time-box">
              <el-date-picker @change="dateChange(true)" v-model="timeData.startTime" type="datetime" placeholder="选择生效时间" :disabled="startTimeDisabled">
              </el-date-picker>
              <div style="margin-left:10px;margin-right:10px">至</div>
              <el-date-picker @change="dateChange(false)" v-model="timeData.endTime" type="datetime" placeholder="选择失效时间">
              </el-date-picker>
            </div>
            <el-alert title="失效时间需要在生效时间之后" type="error" v-if="isTimeError1" :closable="false"></el-alert>
            <el-alert title="不可设置为过去时间" type="error" v-if="isTimeError2" :closable="false"></el-alert>
          </el-form-item>
        </el-form>
      </template>
      <template v-if="basicData.bindType===1">
        <span class="title-class">商城</span>
        <el-form label-position="left" ref="shopData" :model="shopData" label-width="120px" size="small" :rules="shopRules">
          <el-form-item label="选择商城：">
            <div>
              <el-checkbox v-model="shopData.allTree">全选</el-checkbox>
              <span>商场名称</span>
              <span>商场数量</span>
            </div>
            <el-tree :data="shopData.tree" show-checkbox node-key="id" ref="tree">
              <span class="custom-tree-node" slot-scope="{ node, data }">
                <span>{{ node.label }}</span>
                <span>{{ node.number }}</span>
              </span>
            </el-tree>
          </el-form-item>
          <el-form-item label="卡片出现规则：">
            <el-radio-group v-model="shopData.appear">
              <el-radio :label="1">进入时出现</el-radio>
              <el-radio :label="2">离开时出现</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="卡片消失规则：" prop="disappear">
            <div class="text-input">
              <div>离开</div>
              <el-input v-model="shopData.disappear" class="input-class"></el-input>
              <div>分钟消失</div>
            </div>
          </el-form-item>
        </el-form>
        <span class="title-class">机场</span>
        <el-form label-position="left" ref="airportData" :model="airportData" label-width="120px" size="small" :rules="airportRules">
          <el-form-item label="选择商城：">
            <div>
              <el-checkbox v-model="airportData.allTree">全选</el-checkbox>
              <span>机场名称</span>
            </div>
            <el-tree :data="airportData.tree" show-checkbox node-key="id" ref="tree">
            </el-tree>
          </el-form-item>
          <el-form-item label="卡片出现规则：">
            <el-radio-group v-model="airportData.appear">
              <el-radio :label="1">进入时出现</el-radio>
              <el-radio :label="2">离开时出现</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="卡片消失规则：" prop="disappear">
            <div class="text-input">
              <div>离开</div>
              <el-input v-model="airportData.disappear" class="input-class"></el-input>
              <div>分钟消失</div>
            </div>
          </el-form-item>
        </el-form>
        <span class="title-class">地铁</span>
        <el-form label-position="left" ref="metroData" :model="metroData" label-width="120px" size="small" :rules="metroRules">
          <el-form-item label="选择商城：">
            <div>
              <el-checkbox v-model="metroData.allTree">全选</el-checkbox>
              <span>城市名称</span>
              <span>地铁线数</span>
              <span>地铁站数</span>
            </div>
            <el-tree :data="metroData.tree" show-checkbox node-key="id" ref="tree">
            </el-tree>
          </el-form-item>
          <el-form-item label="卡片出现规则：">
            <el-radio-group v-model="metroData.appear">
              <el-radio :label="1">进入时出现</el-radio>
              <el-radio :label="2">离开时出现</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="卡片消失规则：" prop="disappear">
            <div class="text-input">
              <div>离开</div>
              <el-input v-model="metroData.disappear" class="input-class"></el-input>
              <div>分钟消失</div>
            </div>
          </el-form-item>
        </el-form>
        <span class="title-class">家</span>
        <el-form label-position="left" ref="homeData" :model="homeData" label-width="120px" size="small" :rules="homeRules">
          <el-form-item label="卡片出现规则：">
            <el-radio-group v-model="homeData.appear">
              <el-radio :label="1">进入时出现</el-radio>
              <el-radio :label="2">离开时出现</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="卡片消失规则：" prop="disappear">
            <div class="text-input">
              <div>离开</div>
              <el-input v-model="homeData.disappear" class="input-class"></el-input>
              <div>分钟消失</div>
            </div>
          </el-form-item>
        </el-form>
        <span class="title-class">公司</span>
        <el-form label-position="left" ref="companyData" :model="companyData" label-width="120px" size="small" :rules="companyRules">
          <el-form-item label="卡片出现规则：">
            <el-radio-group v-model="companyData.appear">
              <el-radio :label="1">进入时出现</el-radio>
              <el-radio :label="2">离开时出现</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="卡片消失规则：" prop="disappear">
            <div class="text-input">
              <div>离开</div>
              <el-input v-model="companyData.disappear" class="input-class"></el-input>
              <div>分钟消失</div>
            </div>
          </el-form-item>
        </el-form>
        <span class="title-class">作息</span>
        <el-form label-position="left" ref="restData" :model="restData" label-width="120px" size="small" :rules="restRules">
          <el-form-item label="卡片出现规则：">
            <el-radio-group v-model="restData.appear">
              <el-radio :label="1">起床时出现</el-radio>
              <el-radio :label="2">睡觉时出现</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="卡片消失规则：" prop="disappear">
            <div class="text-input">
              <el-input v-model="restData.disappear" class="input-class"></el-input>
              <div>分钟消失</div>
            </div>
          </el-form-item>
        </el-form>
      </template>
      <div class="title2-class">
        <span>排序规则</span>
        <span style="font-size: 10px;">总权值v = a0*初始权值v0 + a1*通知权值v(p) + a2*时间权值v(t)</span>
      </div>
      <el-form label-position="left" ref="weightData" :model="weightData" label-width="120px" size="small" :rules="weightRules" class="text-over">
        <el-form-item label="初始权值(v0)：" prop="order">
          <el-input v-model="weightData.order" placeholder="可填写整数0-50，“0”为优先级最低，“50”为优先级最高" style="width: 350px;" maxlength="2"></el-input>
        </el-form-item>
        <el-form-item label="衰减开始时间点" label-width='200px' style="font-weight: bold">
        </el-form-item>
        <el-form-item label="持续时间：" prop="attenuationDuration" label-width='100px'>
          <el-radio v-model="weightData.attenuationDuration" :label="30" style="margin-left:30px !important">持续0~30分钟</el-radio>
          <el-radio v-model="weightData.attenuationDuration" :label="60">持续0~60分钟</el-radio>
          <el-radio v-model="weightData.attenuationDuration" :label="120">持续0~120分钟</el-radio>
          <el-radio v-model="weightData.attenuationDuration" :label="10120">持续10~120分钟</el-radio>
          <el-radio v-model="weightData.attenuationDuration" :label="180">持续0~180分钟</el-radio>
          <el-radio v-model="weightData.attenuationDuration" :label="240">持续0~240分钟</el-radio>
          <el-radio v-model="weightData.attenuationDuration" :label="920">持续0~920分钟</el-radio>
          <el-radio v-model="weightData.attenuationDuration" :label="1440">持续0~24小时</el-radio>
        </el-form-item>
      </el-form>
    </div>
    <div class="rules-area box-class" v-if="stepNumber===2">
      <span class="confirmtitle-class">发布策略</span>
      <el-form label-position="left" label-width="120px" size="small">
        <el-form-item label="发布方式：" class="formitem-class">
          <span v-if="imeiData.releaseType===1">全量发布</span>
          <span v-if="imeiData.releaseType===2">灰度发布</span>
        </el-form-item>
        <el-form-item label="控制方式：" v-if="imeiData.releaseType===2" class="formitem-class">
          <span v-if="controlType===1">IMEI控制</span>
        </el-form-item>
        <el-form-item label="定向灰度：" v-if="imeiData.releaseType===2" class="formitem-class">
          <span v-if="grayscaleType===1">指定IMEI</span>
        </el-form-item>
        <el-form-item label="上传IMEI方式：" v-if="imeiData.releaseType===2" class="formitem-class">
          <template v-if="imeiData.grayscaleDataType===1">
            <div>手动填写(≤10个)</div>
            <el-tag v-for="(imei, index) in imeiList" :key="index" type="info" style="margin-right: 5px;">
              {{imei}}
            </el-tag>
          </template>
          <template v-if="imeiData.grayscaleDataType===2">
            <div>上传文件(txt格式，每行一个IMEI码，请不要添加其他字符)</div>
            <div v-if="excelFileName">{{excelFileName}}</div>
            <a class='download' :href='downloadFileUrl' download="下载" title="下载" v-if="!excelFileName&&downloadFileName" target="_blank">{{downloadFileName}}</a>
          </template>
        </el-form-item>
        <el-form-item label="尾数灰度：" v-if="imeiData.releaseType===2" class="formitem-class">
          <el-checkbox v-model="isMantissaList1" disabled>使用1个尾数</el-checkbox>
          <el-checkbox-group v-model="mantissaList1" v-if="isMantissaList1" disabled>
            <el-checkbox v-for="n in 10" :label="getForNumber(n,1)" :key="getForNumber(n,1)" style="margin-left:0px !important;margin-right:20px !important">{{getForNumber(n,1)}}</el-checkbox>
          </el-checkbox-group>
          <el-checkbox v-model="isMantissaList2" disabled>使用2个尾数</el-checkbox>
          <el-checkbox-group v-model="mantissaList2" v-if="isMantissaList2" disabled>
            <el-checkbox v-for="n in 100" :label="getForNumber(n,2)" :key="getForNumber(n,2)" style="margin-left:0px !important;margin-right:20px !important">{{getForNumber(n,2)}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <span class="confirmtitle-class">触发与消失规则</span>
      <el-form label-position="left" label-width="120px" size="small">
        <!-- <el-form-item label="人群标签：" class="formitem-class">
                    <div class="text-input">
                        <div class="tip-class" v-if="true">测试</div>
                    </div>
                </el-form-item> -->
        <el-form-item label="绑定维度：" class="formitem-class">
          <span v-if="basicData.bindType===1">按空间</span>
          <span v-if="basicData.bindType===2">按时间</span>
        </el-form-item>
      </el-form>
      <template v-if="basicData.bindType===2">
        <span class="confirmtitle-class">时间选择</span>
        <el-form label-position="left" :model="timeData" label-width="120px" size="small">
          <el-form-item label="时间类型：" class="formitem-class">
            <span v-if="timeData.type===0">手动填写</span>
            <span v-if="timeData.type===1">节假日</span>
          </el-form-item>
          <el-form-item label="节假日选择：" class="formitem-class" v-if="timeData.type===1">
            <div class="time-box">
              <div style="margin-right:30px">{{timeData.holiday}}</div>
              <div>{{getFormatDate(timeData.startTime)}}</div>
              <div style="margin-left:10px;margin-right:10px">至</div>
              <div>{{getFormatDate(timeData.endTime)}}</div>
            </div>
          </el-form-item>
          <el-form-item label="手动填写：" class="formitem-class" v-if="timeData.type===0">
            <div class="time-box">
              <div>{{getFormatDate(timeData.startTime)}}</div>
              <div style="margin-left:10px;margin-right:10px">至</div>
              <div>{{getFormatDate(timeData.endTime)}}</div>
            </div>
          </el-form-item>
        </el-form>
      </template>
      <template v-if="basicData.bindType===1">
        <span class="confirmtitle-class">商场</span>
        <el-form label-position="left" label-width="120px" size="small">
          <el-form-item label="选择：" class="formitem-class">
            {{shopData.treeNames}}
          </el-form-item>
          <el-form-item label="卡片出现规则：" class="formitem-class">
            <span v-if="shopData.appear===1">进入时出现</span>
            <span v-if="shopData.appear===2">离开时出现</span>
          </el-form-item>
          <el-form-item label="卡片消失规则：" class="formitem-class">
            <div class="text-input">
              <div>离开</div>
              {{shopData.disappear}}
              <div>分钟消失</div>
            </div>
          </el-form-item>
        </el-form>
        <span class="confirmtitle-class">机场</span>
        <el-form label-position="left" label-width="120px" size="small">
          <el-form-item label="选择：" class="formitem-class">
            {{airportData.treeNames}}
          </el-form-item>
          <el-form-item label="卡片出现规则：" class="formitem-class">
            <span v-if="airportData.appear===1">进入时出现</span>
            <span v-if="airportData.appear===2">离开时出现</span>
          </el-form-item>
          <el-form-item label="卡片消失规则：" class="formitem-class">
            <div class="text-input">
              <div>离开</div>
              {{airportData.disappear}}
              <div>分钟消失</div>
            </div>
          </el-form-item>
        </el-form>
        <span class="confirmtitle-class">地铁</span>
        <el-form label-position="left" label-width="120px" size="small">
          <el-form-item label="选择：" class="formitem-class">
            {{metroData.treeNames}}
          </el-form-item>
          <el-form-item label="卡片出现规则：" class="formitem-class">
            <span v-if="metroData.appear===1">进入时出现</span>
            <span v-if="metroData.appear===2">离开时出现</span>
          </el-form-item>
          <el-form-item label="卡片消失规则：" class="formitem-class">
