        <el-form-item label="出现时间：" prop="appearTime" class="formitem-class">
          <div class="radio-input">
            <div v-if="noticeData.startType===0">跟随活动出现</div>
            <!-- <div v-if="noticeData.startType===1">活动出现</div>
                            <div v-if="noticeData.startType===1">{{noticeData.appearTime}}</div>
