          </div>
          <el-alert title="请输入1~10080之间的任意整数" type="error" v-if="isNoticeTimeError" :closable="false"></el-alert>
        </el-form-item>
      </el-form>
      <el-button @click="deleteNotice()" type="danger" v-if="noticeDialogVisible" style="margin-bottom: 20px">删除</el-button>
      <el-button @click="addNoticeButton()" type="primary" round class="button-class" plain v-if="!noticeDialogVisible">添加通知<i class="el-icon-plus el-icon--right"></i></el-button>
    </div>
    <div class="box-class" v-if="isDetailpage&&xiajiaReason">
