        <div>{{bohuiReason}}</div>
      </div>
    </div>
    <div class="buttons-area box-class" v-if="isDetailpage">
      <el-button v-if="baseData.state===6" @click="shangjia()" round class="button-class" type="success">上架</el-button>
      <el-button v-if="baseData.state===9" @click="xiajia()" round class="button-class" type="warning">下架</el-button>
      <el-button v-if="baseData.state===9||baseData.state===12" @click="jinyong()" round class="button-class" type="warning">禁用</el-button>
      <el-button v-if="baseData.upperOperateState!==-1&&baseData.upperOperateState!==2&&(baseData.state===0||baseData.state===12||baseData.state===-1)" @click="goEditPage()" round class="button-class" type="primary">编辑</el-button>
      <el-button v-if="baseData.state===0 " @click="handleDelete()" round class="button-class" type="danger">删除</el-button>
      <el-button v-if="baseData.state===9||baseData.state===12||baseData.state===-1" @click="goDataPage()" round class="button-class" type="primary" disabled>查看数据</el-button>
      <el-button @click="goHome()" round class="button-class" type="primary">退出</el-button>
    </div>
    <div class="buttons-area box-class" v-if="isCheckpage">
      <el-button @click="previewTest()" type="primary" round class="button-class">预览测试</el-button>
      <el-button @click="passButton(true)" type="primary" round class="button2-class" :disabled="isExam">通过</el-button>
      <el-button @click="passButton(false)" type="primary" round class="button-class" :disabled="isExam">不通过</el-button>
    </div>
    <div class="buttons-area box-class" v-if="isEditPage&&stepNumber!==3">
      <el-button v-if="stepNumber===1" @click="cancelEditButton()" type="primary" round class="button-class">取消编辑</el-button>
      <el-button v-if="stepNumber===2" @click="lastStepButton()" type="primary" round class="button-class">上一步</el-button>
      <el-button v-if="stepNumber===1" @click="draftButton()" type="primary" round class="button-class">保存为草稿</el-button>
      <el-button v-if="stepNumber===1" @click="nextStepButton()" type="primary" round class="button-class">下一步</el-button>
      <el-button v-if="stepNumber===2" @click="saveButton()" type="primary" round class="button-class">提交</el-button>
    </div>
    <el-dialog title="预览测试" :visible.sync="testDialogVisible" width="600px" center>
      <div class="test-dialog">
