        <img :src="testImgSrc" class="test-img">
        <!-- <span>通过USB调试</span> -->
        <div class="radio-input" style="margin-top: 30px;">
          <span> 需要在手机上先安装测试工具，下载链接和使用方法请点击</span>
          <a class='download' href='http://test-bj-ws.vivo.com.cn/Zlf73fDGXFlrC6ST/2019010315/1f29f8d9e892a32a9aa7ca04b158b054/5c8904df-25a4-499e-9ac8-d02afee86dde.zip' download="下载" title="下载" target="_blank">此处</a>
          <span> 查看</span>
        </div>
      </div>
    </el-dialog>
    <el-dialog title="审核说明" :visible.sync="examDialogVisible" width="600px" :before-close="examDialogClose" center>
      <el-form :model="baseData" ref="examData" label-width="100px">
        <el-form-item label=" 审核说明" prop="reason" :rules="applyRules">
          <el-input v-model="baseData.reason" type="textarea" :autosize="{ minRows: 2, maxRows: 3}" placeholder="请填写审核说明" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{baseData.reason?baseData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="examDialogClose(true)">取 消</el-button>
        <el-button type="primary" @click="examDialogClose()">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="禁用申请" :visible.sync="jinyongDialogVisible" width="600px" :before-close="jinyongDialogClose" center>
      <el-form :model="baseData" ref="jinyongData" label-width="100px">
        <el-form-item label=" 禁用原因" prop="reason" :rules="applyRules">
          <el-input v-model="baseData.reason" type="textarea" :autosize="{ minRows: 2, maxRows: 3}" placeholder="请填写禁用原因" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{baseData.reason?baseData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="jinyongDialogClose(true)">取 消</el-button>
        <el-button type="primary" @click="jinyongDialogClose()">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="下架申请" :visible.sync="xiajiaDialogVisible" width="600px" :before-close="xiajiaDialogClose" center>
      <el-form :model="baseData" ref="xiajiaData" label-width="100px">
        <el-form-item label="下架原因" prop="reason" :rules="applyRules">
          <el-input v-model="baseData.reason" :autosize="{ minRows: 2, maxRows: 3}" type="textarea" placeholder="请填写下架原因" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{baseData.reason?baseData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="xiajiaDialogClose(true)">取 消</el-button>
        <el-button type="primary" @click="xiajiaDialogClose()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import global from "../service/global";

export default {
  name: "activity-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    },
    isDetailpage: {
      type: Boolean,
      default: false
    },
    baseData: {}
  },
  data() {
    var weightValidate = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入初始权值"));
      } else if (/^\d+$/.test(value)) {
        let newValue = Number(value);
        if (newValue >= 0 && newValue <= 50) {
          callback();
        } else {
          callback(new Error("请输入整数0-50"));
        }
      } else {
        callback(new Error("请输入整数0-50"));
      }
    };
    var specialValidate = (rule, value, callback) => {
      if (/^\s+$/.test(value)) {
        callback(new Error("不能输入全空格"));
      } else {
        callback();
      }
    };
    return {
      grayscaleDataTypes: [],
      excelFileName: "",
      excelFile: "",
      uploadLoading: false,
      controlType: 1,
      grayscaleType: 1,
      downloadFileName: '',
      downloadFileUrl: '',
      isMantissaList1: false,
      isMantissaList2: false,
      imeiList: [],
      imeiValue: "",
      imeiData: {
        grayscaleDataType: '',
        releaseType: ''
      },
      mantissaList1: [],
      mantissaList2: [],
      isNoticeTimeError: false,
      isTimeError1: false,
      isTimeError2: false,
      startTimeDisabled: false,
      testCardUrl: "",
      isExam: false,
      xiajiaDialogVisible: false,
      jinyongDialogVisible: false,
      isEditPage: true,
      examDialogVisible: false,
      testImgSrc: "",
      testDialogVisible: false,
      xiajiaReason: "",
      jinyongReason: "",
      bohuiReason: "",
      noticeData: {
        title: "",
        content: "",
        startType: "",
        endType: "",
        endTime: "",
        timeSwitch: false
      },
      noticeDialogVisible: false,
      cardPreview: "",
      expandPreview: "",
      holidayList: global.holidayList,
      //peopleLabels: [{ key: 1, value: "年轻人" }, { key: 2, value: "老年人" }],
      stepNumber: 1,
      basicData: {
        peopleLabels: [],
        bindType: 2
      },
      timeData: {
        type: 0,
        startTime: "",
        endTime: "",
        holiday: ""
      },
      shopData: {
        allTree: false,
        tree: [],
        appear: "",
        disappear: "",
        treeNames: ""
      },
      airportData: {
        allTree: false,
        tree: [],
        appear: "",
        disappear: "",
        treeNames: ""
      },
      metroData: {
        allTree: false,
        tree: [],
        appear: "",
        disappear: "",
        treeNames: ""
      },
      homeData: {
        appear: "",
        disappear: ""
      },
      companyData: {
        appear: "",
        disappear: ""
      },
      restData: {
        appear: "",
        disappear: ""
      },
      weightData: {
        order: "",
        attenuationDuration: ""
      },
      peopleRules: {
        // peopleLabels: [
        //   { required: true, message: "请选择人群标签", trigger: "change" }
        // ],
        bindType: [
          { required: true, message: "请选择绑定维度", trigger: "change" }
        ]
      },
      shopRules: {},
      airportRules: {},
      metroRules: {},
      homeRules: {},
      companyRules: {},
      restRules: {},
      timeRules: {},
      noticeRules: {
        title: [
          { required: true, message: "请输入通知标题", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" }
        ],
        content: [
          { required: true, message: "请输入通知内容", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" }
        ]
      },
      weightRules: {
        order: [{ required: true, validator: weightValidate, trigger: "blur" }],
        attenuationDuration: [
          { required: true, message: '请选择持续时间', trigger: 'change' }
        ],
      },
      applyRules: [
        { required: true, message: "内容不能为空", trigger: "blur" },
        { required: true, validator: specialValidate, trigger: "blur" }
      ]
    };
  },
  methods: {
    grayscaleDataTypeChange(arrary) {
      if (arrary.length > 1) {
        this.grayscaleDataTypes.shift();
      }
      if (this.grayscaleDataTypes.length > 0) {
        this.imeiData.grayscaleDataType = this.grayscaleDataTypes[0];
      } else {
        this.imeiData.grayscaleDataType = '';
      }
    },
    deleteIMEI(index) {
      this.imeiList.splice(index, 1);
    },
    saveIMEI() {
      if (this.imeiList.length > 9) {
        this.$confirm("10个以上IMEI码请通过文件上传", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      } else if (this.imeiList.includes(this.imeiValue)) {
        this.$confirm("IMEI码重复！", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      } else if (/^\d{15}$/.test(this.imeiValue)) {
        this.imeiList.push(this.imeiValue);
      } else {
        this.$confirm("IMEI码不符合要求！(15位数字)", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      }
    },
    getForNumber(number, type) {
      let newNumber = number - 1;
      if (type === 1) {
        return newNumber + '';
      } else {
        if (newNumber < 10) {
          return '0' + newNumber;
        } else {
          return newNumber + '';
        }
      }
    },
    //详情页面多文件上传提醒
    handleExceed() {
      this.$message.warning("一次只能上传一个文件，请先删掉当前文件");
    },
    removeFile() {
      this.excelFile = "";
      this.excelFileName = "";
    },
    //上传文件后回调
    getExcelFile(data) {
      this.excelFile = data.file;
      this.excelFileName = data.file.name;
    },
    getFormatDate(date) {
      if (date) {
        let newDate = "";
        if (typeof date === 'string') {
          newDate = new Date(Date.parse(date.replace(/-/g, "/")));
        } else {
          newDate = new Date(date);
        }
        return newDate.Format("yyyy-MM-dd hh:mm:ss");
      } else {
        return "";
      }
    },
    numberChange(value) {
      if (/^\d+$/.test(value)) {
        let number = Number(value);
        if (number >= 1 && number <= 10080) {
          this.isNoticeTimeError = false;
        } else {
          this.isNoticeTimeError = true;
        }
      } else {
        this.isNoticeTimeError = true;
      }
    },
    dateChange(isStart) {
      let startDate = Date.parse(this.timeData.startTime);
      let endDate = Date.parse(this.timeData.endTime);
      let now = Date.now();
      if (isStart && startDate < now) {
        this.isTimeError2 = true;
        this.isTimeError1 = false;
      } else if (!isStart && endDate < now) {
        this.isTimeError2 = true;
        this.isTimeError1 = false;
      } else if (startDate >= endDate) {
        this.isTimeError1 = true;
        this.isTimeError2 = false;
      } else {
        this.isTimeError1 = false;
        this.isTimeError2 = false;
      }
    },
    noticeRadio2Choose() {
      this.noticeData.endTime = 120;
    },
    timeRadioChoose() {
      this.timeData.holiday = "";
      this.timeData.startTime = "";
      this.timeData.endTime = "";
      this.isTimeError1 = false;
      this.isTimeError2 = false;
    },
    //节日选择变更后
    holidayChange(value) {
      let holiday = this.holidayList.find(holiday => {
        return holiday.value === value;
      });
      if (holiday) {
        this.timeData.startTime = holiday.startTime;
        this.timeData.endTime = holiday.endTime;
      }
    },
    //删除操作
    handleDelete() {
      let message = "是否确定删除“" + this.baseData.name + "”的草稿？";
      this.$confirm(message, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          let param = {
            id: this.baseData.id,
            versionCode: this.baseData.versionCode
          };
          this.$http
            .post("/scene/del", param)
            .then(response => {
              if (response.data && response.data.code == "0") {
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                this.$emit("activity-event", {
                  type: "home-page"
                });
              } else {
                if (response.data.msg) {
                  this.$message.error(response.data.msg);
                } else {
                  this.$message.error("删除失败！");
                }
              }
            })
            .catch(() => {
              this.$message.error("删除失败！");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    //下架弹窗
    xiajiaDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.xiajiaDialogVisible = false;
      } else {
        this.$refs["xiajiaData"].validate(valid => {
          if (valid) {
            let param = {
              id: this.baseData.id,
              versionCode: this.baseData.versionCode,
              lastState: this.baseData.state,
              state: 12,
              explain: this.baseData.reason
            };
            this.$http
              .post("/scene/modifyState", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: "下架成功"
                  });
                  this.xiajiaReason = this.baseData.reason;
                  this.baseData.reason = "";
                  this.baseData.state = 12;
                  this.xiajiaDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("下架失败！");
                  }
                }
              })
              .catch(() => {
                this.$message.error("下架失败！");
              });
          } else {
            this.$message.warning("请按要求填写");
            return false;
          }
        });
      }
    },
    //下架操作
    xiajia() {
      this.xiajiaDialogVisible = true;
    },
    //上架操作
    shangjia() {
      let param = {
        id: this.baseData.id,
        versionCode: this.baseData.versionCode,
        lastState: this.baseData.state,
        state: 9
      };
      this.$http
        .post("/scene/modifyState", param)
        .then(response => {
          if (response.data && response.data.code == "0") {
            this.baseData.state = 9;
            this.$message({
              type: "success",
              message: "上架成功"
            });
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("上架失败！");
            }
          }
        })
        .catch(() => {
          this.$message.error("上架失败！");
        });
    },
    //审核操作
    passButton(type) {
      if (type) {
        let param = {
          id: this.baseData.id,
          versionCode: this.baseData.versionCode,
          lastState: this.baseData.state,
          state: 6,
          explain: this.baseData.reason
        };
        this.$http
          .post("/scene/modifyState", param)
          .then(response => {
            if (response.data && response.data.code == "0") {
              this.$message({
                type: "success",
                message: "操作成功"
              });
              this.baseData.state = 6;
              this.isExam = true;
              this.$emit("activity-event", {
                type: "home-page"
              });
            } else {
              if (response.data.msg) {
                this.$message.error(response.data.msg);
              } else {
                this.$message.error("操作失败！");
              }
            }
          })
          .catch(() => {
            this.$message.error("操作失败！");
          });
      } else {
        this.examDialogVisible = true;
      }
    },
    //禁用弹窗
    jinyongDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.jinyongDialogVisible = false;
      } else {
        this.$refs["jinyongData"].validate(valid => {
          if (valid) {
            let param = {
              id: this.baseData.id,
              versionCode: this.baseData.versionCode,
              lastState: this.baseData.state,
              state: -1,
              explain: this.baseData.reason
            };
            this.$http
              .post("/scene/modifyState", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: "禁用成功"
                  });
                  this.jinyongReason = this.baseData.reason;
                  this.baseData.reason = "";
                  this.baseData.state = -1;
                  this.jinyongDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("禁用失败！");
                  }
                }
              })
              .catch(() => {
                this.$message.error("禁用失败！");
              });
          } else {
            this.$message.warning("请按要求填写");
            return false;
          }
        });
      }
    },
    //禁用操作
    jinyong() {
      this.jinyongDialogVisible = true;
    },
    //预览测试操作
    previewTest() {
      let url = location.origin + "/scene/getQrCode?url=" + this.testCardUrl;
      this.testImgSrc = url;
      this.testDialogVisible = true;
    },
    //审核弹窗
    examDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.examDialogVisible = false;
      } else {
        this.$refs["examData"].validate(valid => {
          if (valid) {
            let param = {
              id: this.baseData.id,
              versionCode: this.baseData.versionCode,
              lastState: this.baseData.state,
              state: 0,
              explain: this.baseData.reason
            };
            this.$http
              .post("/scene/modifyState", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: "操作成功"
                  });
                  this.baseData.state = 0;
                  this.isExam = true;
                  this.examDialogVisible = false;
                  this.$emit("activity-event", {
                    type: "home-page"
                  });
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("操作失败！");
                  }
                }
              })
              .catch(() => {
                this.$message.error("操作失败！");
              });
          } else {
            this.$message.warning("请按要求填写");
            return false;
          }
        });
      }
    },
    //跳转数据页面
    goDataPage() {
      this.$emit("activity-event", {
        type: "data-page"
      });
    },
    //跳转编辑页面
    goEditPage() {
      let endTime = Date.parse(this.baseData.endTime);
      if (this.baseData.state !== 0 && Date.now() > endTime) {
        this.$confirm("活动已结束, 无法编辑！", "提示", {
          confirmButtonText: "确定",
          showCancelButton: false,
          type: "warning",
          center: true
        });
      } else {
        this.stepNumber = 1;
        this.isEditPage = true;
        this.isCheckpage = false;
        this.isDetailpage = false;
      }
    },
    //删除通知数据
    deleteNotice() {
      this.noticeData = {
        title: "",
        content: "",
        startType: "",
        endType: "",
        endTime: "",
        timeSwitch: false
      };
      this.noticeDialogVisible = false;
    },
    //打开添加通知弹窗
    addNoticeButton() {
      this.noticeDialogVisible = true;
    },
    //回到列表页面
    goHome() {
      this.$emit("activity-event", {
        type: "home-page"
      });
    },
    isEmpty(key) {
      if (typeof key === 'undefined' || key === '' || key === null) {
        return true;
      } else {
        return false;
      }
    },
    dataCheck(canEmpty) {
      //权值检查
      if (!canEmpty || !this.isEmpty(this.weightData.order)) {
        if (!/^\d+$/.test(this.weightData.order) || Number(this.weightData.order) < 0 || Number(this.weightData.order) > 50) {
          this.$confirm("初始权值不符合要求，请重新填写！", "提示", {
            confirmButtonText: "确定",
            center: true,
            type: "warning"
          });
          return false;
        }
      }
      //通知标题检查
      if (this.noticeDialogVisible && (!canEmpty || !this.isEmpty(this.noticeData.title))) {
        if (/^\s+$/.test(this.noticeData.title) || this.isEmpty(this.noticeData.title)) {
          this.$confirm("通知标题不符合要求，请重新填写！", "提示", {
            confirmButtonText: "确定",
            center: true,
            type: "warning"
          });
          return false;
        }
      }
      //通知内容检查
      if (this.noticeDialogVisible && (!canEmpty || !this.isEmpty(this.noticeData.content))) {
        if (/^\s+$/.test(this.noticeData.content) || this.isEmpty(this.noticeData.content)) {
          this.$confirm("通知内容不符合要求，请重新填写！", "提示", {
            confirmButtonText: "确定",
            center: true,
            type: "warning"
          });
          return false;
        }
      }
      //消失时间检查
      if (this.noticeDialogVisible && (!canEmpty || !this.isEmpty(this.noticeData.endTime))) {
        if (this.isNoticeTimeError) {
          this.$confirm("通知消失时间不符合要求，请重新填写！", "提示", {
            confirmButtonText: "确定",
            center: true,
            type: "warning"
          });
          return false;
        }
      }

      if (!canEmpty) {
        //通知出现类型
        if (this.noticeDialogVisible && this.isEmpty(this.noticeData.startType)) {
          this.$confirm("通知出现时间未选择！", "提示", {
            confirmButtonText: "确定",
            center: true,
            type: "warning"
          });
          return false;
        }
        //通知消失类型
        if (this.noticeDialogVisible && this.isEmpty(this.noticeData.endType)) {
          this.$confirm("通知消失时间未选择！", "提示", {
            confirmButtonText: "确定",
            center: true,
            type: "warning"
          });
          return false;
        }
        //时间类型
        if (this.isEmpty(this.timeData.type)) {
          this.$confirm("时间选择未选择！", "提示", {
            confirmButtonText: "确定",
            center: true,
            type: "warning"
          });
          return false;
        }
        //绑定维度
        if (this.isEmpty(this.basicData.bindType)) {
          this.$confirm("绑定维度未选择！", "提示", {
            confirmButtonText: "确定",
            center: true,
            type: "warning"
          });
          return false;
        }
        //持续时间
        if (this.isEmpty(this.weightData.attenuationDuration)) {
          this.$confirm("持续时间未选择！", "提示", {
            confirmButtonText: "确定",
            center: true,
            type: "warning"
          });
          return false;
        }
        //时间选择检查
        if (this.isEmpty(this.timeData.startTime) || this.isEmpty(this.timeData.endTime) || this.isTimeError1 || this.isTimeError2) {
          this.$confirm("时间选择不符合要求，请重新选择！", "提示", {
            confirmButtonText: "确定",
            center: true,
            type: "warning"
          });
          return false;
        }
        //发布策略选择
        if (!this.imeiData.releaseType) {
          this.$confirm("发布方式未选择，请重新选择！", "提示", {
            confirmButtonText: "确定",
            center: true,
            type: "warning"
          });
          return false;
        }
        //灰度方式选择
        if (this.imeiData.releaseType === 2) {
          let mantissaList1Ready = this.isMantissaList1 && this.mantissaList1.length > 0 ? true : false;
          let mantissaList2Ready = this.isMantissaList2 && this.mantissaList2.length > 0 ? true : false;
          let imeiListReady = this.imeiData.grayscaleDataType === 1 && this.imeiList.length > 0 ? true : false;
          let excelFileReady = this.imeiData.grayscaleDataType === 2 && this.excelFile ? true : false;
          if (!(mantissaList1Ready || mantissaList2Ready || imeiListReady || excelFileReady)) {
            this.$confirm("上传IMEI方式和尾数灰度至少选择一种！", "提示", {
              confirmButtonText: "确定",
              center: true,
              type: "warning"
            });
            return false;
          }
        }
      }
      return true;
    },
    //保存草稿
    draftButton() {
      if (this.dataCheck(true)) {
        this.prepareEditData();
      }
    },
    //保存操作
    saveButton() {
      this.prepareEditData();
    },
    //准备草稿或者编辑数据
    async prepareEditData() {
      let currentState = 0;
      if (this.stepNumber === 2) {
        currentState = 3;
      }

      let timeData = {};
      timeData.type = this.timeData.type;
      timeData.holiday = this.timeData.holiday;
      timeData.startTime = this.getFormatDate(this.timeData.startTime);
      timeData.endTime = this.getFormatDate(this.timeData.endTime);

      let rule = {
        order: this.weightData.order,
        attenuationDuration: this.weightData.attenuationDuration,
        timeRule: timeData,
        state: currentState
      };

      let imeiData = {
        releaseType: this.imeiData.releaseType,
      };
      if (this.imeiData.releaseType === 2) {
        if (this.imeiData.grayscaleDataType === 1) {
          imeiData.imeiList = this.imeiList;
        } else if (this.imeiData.grayscaleDataType === 2 && this.excelFileName && this.excelFile) {
          this.uploadLoading = true;
          imeiData.fileName = this.excelFileName;
          imeiData.fileId = await this.uploadExcel();
        }
        if (this.isMantissaList1) {
          imeiData.mantissaList1 = this.mantissaList1;
        }
        if (this.isMantissaList2) {
          imeiData.mantissaList2 = this.mantissaList2;
        }
      }

      let param = {
        activityId: this.baseData.id,
        versionCode: this.baseData.versionCode,
        currentState: currentState,
        lastState: 0,
        rule: rule,
        imeiData: imeiData
      };

      if (this.noticeDialogVisible) {
        if (this.noticeData.endType === 0) {
          this.noticeData.endTime = 0;
        } else {
          this.noticeData.endTime = Number(this.noticeData.endTime);
        }
        param.noti = this.noticeData;
      }

      if ((param.imeiData.fileName && param.imeiData.fileId) || !param.imeiData.fileName) {
        this.saveDataXhr(param);
      }
    },
    //上传excel文件
    async uploadExcel() {
      let formData = new FormData();
      formData.append("activityId", this.baseData.id);
      formData.append("imeiFile", this.excelFile);
      return new Promise((resolve) => {
        this.$http
          .post('/scene/upoloadGreyImeiFile', formData)
          .then(response => {
            if (response.data && response.data.code == 0) {
              let fileId = response.data.data;
              resolve(fileId);
            } else {
              this.uploadLoading = false;
              if (response.data.msg) {
                this.$message.error(response.data.msg);
              } else {
                this.$message.error("操作失败！");
              }
              resolve('');
            }
          })
          .catch(() => {
            this.uploadLoading = false;
            this.$message.error("操作失败！");
            resolve('');
          });
      });
    },
    //保存数据xhr
    saveDataXhr(param) {
      this.$http
        .post("/scene/editRuleNNoti", param)
        .then(response => {
          this.uploadLoading = false;
          if (response.data && response.data.code == "0") {
            if (this.stepNumber === 1) {
              this.$message({
                type: "success",
                message: "草稿保存成功"
              });
            } else {
              this.stepNumber++;
            }
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("操作失败！");
            }
          }
        })
        .catch(() => {
          this.uploadLoading = false;
          this.$message.error("操作失败！");
        });
    },
    //上一步操作按钮
    lastStepButton() {
      this.stepNumber--;
    },
    //下一步操作按钮
    nextStepButton() {
      if (this.dataCheck(false)) {
        this.stepNumber++;
      }
    },
    //计算延后时长对应时间
    timeCompute(date, time) {
      let d = new Date(date);
      let d2 = d.getTime() + time * 60000;
      let d3 = new Date(d2);
      return d3.Format("yyyy-MM-dd hh:mm:ss");
    },
    //取消编辑操作
    cancelEditButton() {
      this.$confirm("是否确定取消编辑?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "返回",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$emit("activity-event", {
            type: "home-page"
          });
        })
        .catch(() => { });
    },
    //获取数据xhr
    getDataXhr() {
      let param = {
        activityId: this.baseData.id,
        versionCode: this.baseData.versionCode
      };
      this.$http
        .get("/scene/getActivityInfo", { params: param })
        .then(response => {
          if (
            response.data &&
            response.data.code == "0" &&
            response.data.data
          ) {
            let activityDTO = response.data.data;
            this.testCardUrl = activityDTO.cardUrl;
            this.cardPreview = activityDTO.cardPreview;
            this.expandPreview = activityDTO.expandPreview;
            if (this.baseData.state === 12) {
              this.xiajiaReason = activityDTO.modifyReason;
            } else if (this.baseData.state === -1) {
              this.jinyongReason = activityDTO.modifyReason;
            } else if (this.baseData.state === 0) {
              this.bohuiReason = activityDTO.modifyReason;
            }
            if (activityDTO.noti) {
              this.noticeData = activityDTO.noti;
              this.noticeDialogVisible = true;
            }
            if (activityDTO.rule) {
              this.timeData = typeof activityDTO.rule.timeRule !== 'undefined'
                ? activityDTO.rule.timeRule
                : {};
              this.weightData.order = typeof activityDTO.rule.order !== 'undefined'
                ? activityDTO.rule.order
                : "";
              this.weightData.attenuationDuration = typeof activityDTO.rule.attenuationDuration !== 'undefined'
                ? activityDTO.rule.attenuationDuration
                : "";
              if (Date.parse(this.timeData.startTime) < Date.now()) {
                this.startTimeDisabled = true;
              }
            }
            if (activityDTO.imeiData) {
              this.imeiData.releaseType = activityDTO.imeiData.releaseType;
              if (this.imeiData.releaseType === 2) {
                if (activityDTO.imeiData.imeiList && activityDTO.imeiData.imeiList.length > 0) {
                  this.imeiList = activityDTO.imeiData.imeiList;
                  this.imeiData.grayscaleDataType = 1;
                  this.grayscaleDataTypes = [1];
                } else if (activityDTO.imeiData.fileName) {
                  this.downloadFileUrl = activityDTO.imeiData.fileId;
                  this.downloadFileName = activityDTO.imeiData.fileName;
                  this.imeiData.grayscaleDataType = 2;
                  this.grayscaleDataTypes = [2];
                }
                if (activityDTO.imeiData.mantissaList1 && activityDTO.imeiData.mantissaList1.length > 0) {
                  this.mantissaList1 = activityDTO.imeiData.mantissaList1;
                  this.isMantissaList1 = true;
                }
                if (activityDTO.imeiData.mantissaList2 && activityDTO.imeiData.mantissaList2.length > 0) {
                  this.mantissaList2 = activityDTO.imeiData.mantissaList2;
                  this.isMantissaList2 = true;
                }
              }
            }
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //获取时刻对应的延后时长
    getDisappearTime(date, afterDate) {
      let d1 = new Date(date);
      let d2 = new Date(afterDate);
      let t = d2.getTime() - d1.getTime();
      if (t > 0) {
        return Math.round(t / 60000);
      }
    }
  },
  //初始化请求页面数据
  mounted: function () {
    if (this.isCheckpage || this.isDetailpage) {
      this.isEditPage = false;
      this.stepNumber = 2;
    }
    if (typeof this.baseData.id !== 'undefined') {
      this.getDataXhr();
    }
  },
  created: function () {
    //this.getDataXhr();
  }
};
</script>

<style scoped>
.activity-container {
  min-height: 100%;
  width: 1000px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 40px;
  padding-right: 40px;
  background-color: rgb(226, 226, 226);
  padding-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
}
.main-title {
  height: 40px;
  width: 100%;
  display: flex;
  font-size: 25px;
  font-weight: bold;
  margin-top: 10px;
}
.title-class {
