<template>
  <div class="body-container">
    <header-component v-if="isTableShow" @header-event="headerEvent" :is-checkpage="isCheckPage"></header-component>
    <table-component v-if="isTableShow" :data-list="dataList" @table-event="tableEvent" :is-checkpage="isCheckPage"></table-component>
    <footer-component v-if="isTableShow" :data-total="dataTotal" :current-page="pageIndex" :page-size="pageSize" @footer-event="footerEvent"></footer-component>
    <activity-Component v-if="isActivityShow" :base-data="baseData" @activity-event="activityEvent" :is-detailpage="isDetailPage" :is-checkpage="isCheckPage"></activity-Component>
  </div>
</template>

<script>
import tableComponent from "./components/TableComponent.vue";
import headerComponent from "./components/HeaderComponent.vue";
import footerComponent from "./components/FooterComponent.vue";
import activityComponent from "./components/ActivityComponent.vue";

export default {
  name: "app",
  components: {
    tableComponent,
    headerComponent,
    footerComponent,
    activityComponent
  },
  data() {
    return {
      isActivityShow: false,
      isDetailPage: false,
      isTableShow: true,
      isCheckPage: false,

      dataList: [],
      pageIndex: 1,
      pageSize: 20,
      dataTotal: 0,
      searchName: "",
      searchType: "",
      searchState: "",
      searchField: "",
      searchOrder: "",

      baseData: {}
    };
  },
  methods: {
    //活动管理
    activityEvent(event) {
      switch (event.type) {
        case "home-page":
          this.getDataXhr();
          this.isActivityShow = false;
          this.isTableShow = true;
          break;
        case "data-page":
          //跳转大数据页面
          break;
        default:
          break;
      }
    },
    //表格事件
    tableEvent(event) {
      switch (event.type) {
        case "data-page":
          //跳转数据页面
          break;
        case "edit-page":
          this.baseData = event.data;
          this.isTableShow = false;
          this.isDetailPage = false;
          this.isCheckPage = false;
          this.isActivityShow = true;
          break;
        case "detail-page":
          this.baseData = event.data;
          this.isTableShow = false;
          this.isDetailPage = true;
          this.isCheckPage = false;
          this.isActivityShow = true;
          break;
        case "check-page":
          this.baseData = event.data;
          this.isTableShow = false;
          this.isDetailPage = false;
          this.isCheckPage = true;
          this.isActivityShow = true;
          break;
        case "refresh-data":
          this.getDataXhr();
          break;
        case "sortChange":
          this.searchOrder = event.data.order;
          this.searchField = event.data.field;
          this.getDataXhr();
          break;
        default:
          break;
      }
    },
    //页头事件
    headerEvent(event) {
      this.searchData(event.data);
    },
    //搜索请求
    searchData(data) {
      this.searchName = data.name;
      this.searchType = data.type;
      if (this.isCheckPage) {
        this.searchState = 3;
      } else {
        this.searchState = data.state;
      }
      this.pageIndex = 1;
      this.getDataXhr();
    },
    //获取数据xhr
    getDataXhr() {
      var param = {
        name: this.searchName === "" ? undefined : this.searchName,
        state: this.searchState === "" ? undefined : this.searchState,
        type: this.searchType === "" ? undefined : this.searchType,
        field: this.searchField === "" ? undefined : this.searchField,
        order: this.searchOrder === "" ? undefined : this.searchOrder,
        page: this.pageIndex,
        size: this.pageSize
      };
      this.$http
        .post("/scene/findActivity", param)
        .then(response => {
          this.dataTotal =
            response.data.data && response.data.data.totalElements;
          this.dataList = response.data.data && response.data.data.content;
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //页脚事件
    footerEvent(event) {
      switch (event.type) {
        case "pageChange":
          this.pageIndex = event.data;
          this.getDataXhr();
          break;
        case "sizeChange":
          this.pageIndex = 1;
          this.pageSize = event.data;
          this.getDataXhr();
          break;
        default:
          break;
      }
    }
  },
  created: function () {
    if (this.isCheckPage) {
      this.searchState = 3;
    }
    this.getDataXhr();
  }
};
</script>

<style scoped>
.body-container {
  min-height: 100%;
  padding-left: 70px;
  padding-right: 70px;
}
</style>
