/* eslint-disable */
<template>
  <div class="header-class">
    <div class="left-input">
      <el-input placeholder="活动名称" v-model="activityName" clearable></el-input>
    </div>
    <el-select v-model="typeKey" clearable placeholder="页面类型" class="left-select">
      <el-option v-for="item in typeList" :key="item.key" :label="item.name" :value="item.key">
      </el-option>
    </el-select>
    <el-select v-model="statusKey" clearable placeholder="状态" class="left-select" v-if="!isCheckpage">
      <el-option v-for="item in statusList" :key="item.key" :label="item.name" :value="item.key">
      </el-option>
    </el-select>
    <el-button @click="searchData" icon="el-icon-search" class="left-button" type="primary">搜索</el-button>
  </div>
</template>

<script>
export default {
  name: "header-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activityName: "",
      typeKey: "",
      statusKey: "",
      typeList: [{ key: 1, name: "卡片" }, { key: 2, name: "开屏页面" }],
      statusList: [
        { key: -1, name: "禁用" },
        { key: 0, name: "草稿" },
        { key: 3, name: "待审核" },
        { key: 6, name: "待上架" },
        { key: 9, name: "已上架" },
        { key: 12, name: "已下架" }
      ]
    };
  },
  methods: {
    searchData() {
      this.$emit("header-event", {
        type: "search-event",
        data: {
          name: this.activityName,
          type: this.typeKey,
          state: this.statusKey
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header-class {
  width: 100%;
  height: 50px;
  margin-top: 30px;
}
.left-input {
  float: left;
  margin-left: 10px;
  width: 200px;
}
.left-button {
  float: left;
  margin-left: 20px;
}
.left-select {
  float: left;
  margin-left: 20px;
  width: 120px;
}
</style>